.companies__container {
  display: flex;
  justify-content: space-between;
  gap: 110px;
}
@media (max-width: 768px) {
  .companies__container {
    flex-direction: column;
    gap: 48px;
  }
}

.companies__card-text {
  font-size: 12px;
  line-height: 20px;
  font-family: "Sora", sans-serif;
  font-weight: 400;
}

.companies__body {
  max-width: 646px;
}
@media (max-width: 992.99px) {
  .companies__body {
    text-align: center;
  }
}
@media (max-width: 992.99px) and (max-width: 600px) {
  .companies__body {
    text-align: start;
  }
}

.companies__cards {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
@media (max-width: 768px) {
  .companies__cards {
    align-items: center;
  }
}

.companies__body-title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: 0px;
}
@media (max-width: 768px) {
  .companies__body-title {
    font-size: 32px;
    line-height: 40px;
  }
}

.companies__body-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
  padding-block-start: 16px;
}

.companies__body-img {
  padding: 15px;
  border-radius: 22px;
  background-color: #222233;
}

.companies__body-img--green {
  background-color: #57efb4;
}

.companies__body-img img {
  width: 15px;
  height: 15px;
}

.companies__card {
  display: flex;
  align-items: center;
  padding: 20px;
  max-width: 439px;
  min-width: 200px;
}
@media (max-width: 1025px) {
  .companies__card {
    max-width: 296px;
  }
}
@media (max-width: 992.99px) {
  .companies__card {
    min-width: 200px;
    max-width: 350px;
    width: 100%;
  }
}
.companies__info {
  padding-inline-start: 24px;
}

.companies__column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 992.99px) {
  .companies__column {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .companies__column {
    width: 100%;
    max-width: 100%;
  }
}
@media (max-width: 600px) {
  .companies__column {
    text-align: start;
  }
}

.companies__column--text {
  max-width: 646px;
}
@media (max-width: 1025px) {
  .companies__column--text {
    max-width: 438px;
  }
}
@media (max-width: 768px) {
  .companies__column--text {
    max-width: 100%;
  }
}

.companies__column--cards {
  max-width: 438px;
  width: 100%;
}
@media (max-width: 1025px) {
  .companies__column--cards {
    max-width: 290px;
  }
}
@media (max-width: 768px) {
  .companies__column--cards {
    max-width: 100%;
  }
}